import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';

@Component({
  selector: 'app-historia-clinica-form-examen-fisico',
  templateUrl: './historia-clinica-form-examen-fisico.component.html',
  styleUrls: ['./historia-clinica-form-examen-fisico.component.css']
})
export class HistoriaClinicaFormExamenFisicoComponent implements OnInit {

  pacientes: any = [];
  edit = false;
  link = '';



  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('',),
      temperatura : new FormControl('',),
      presion_arterial : new FormControl('',),
      frecuencia_cardiaca : new FormControl('',),
      frecuencia_respiratoria : new FormControl('',),
      peso : new FormControl('',),
      talla : new FormControl('',),
      imc : new FormControl('',),
      piel : new FormControl('',),
      musculo_esqueletico : new FormControl('',),
      cabeza_cuello : new FormControl('',),
      ojos : new FormControl('',),
      nariz : new FormControl('',),
      boca : new FormControl('',),
      oidos : new FormControl('',),
      torax : new FormControl('',),
      abdomen : new FormControl('',),
      neurologico : new FormControl('',),
      linfatico : new FormControl('',),
 
        
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
     
     
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
        
      this.link = params.id;

              this.comboListService.getExamenFisico(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                          paciente_id: this.pacientes.paciente_id,
                          temperatura : this.pacientes.temperatura,
                          presion_arterial : this.pacientes.presion_arterial,
                          frecuencia_cardiaca : this.pacientes.frecuencia_cardiaca,
                          frecuencia_respiratoria : this.pacientes.frecuencia_respiratoria,
                          peso : this.pacientes.peso,
                          talla : this.pacientes.talla,
                          imc : this.pacientes.imc,
                          piel : this.pacientes.piel,
                          musculo_esqueletico : this.pacientes.musculo_esqueletico,
                          cabeza_cuello : this.pacientes.cabeza_cuello,
                          ojos : this.pacientes.ojos,
                          nariz : this.pacientes.nariz,
                          boca : this.pacientes.boca,
                          oidos : this.pacientes.oidos,
                          torax : this.pacientes.torax,
                          abdomen : this.pacientes.abdomen,
                                                          
                      
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
    }


    Guardar() {
      const params = this.activatedRoute.snapshot.params;

      this.Form.get('paciente_id').setValue(params.id);
      
      this.comboListService.saveExamenFisicos(this.Form.value).subscribe(
        res => {
              console.log(res);
             //this.router.navigate(['/tiposvacunas/admin/list']);
              Swal.fire(
                'Exitoso!',
                'Datos guardados correctamente...!',
                'success'
              );
              this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        } 
      );
    }

    Actualizar() {

      this.comboListService.updateExamenFisicos(this.pacientes.id, this.pacientes)
        .subscribe(
          res => {
            console.log('actualizando', res);
           // this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos actualizados correctamente...!',
              'success'
            );
            this.ngOnInit();
          },
          err => {
            console.error(err);
            alert('Error!');

          }
        );
    }



}
