import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HistoriaClinicaCedulaFormComponent } from './components/admin/historia-clinica/historia-clinica-cedula-form/historia-clinica-cedula-form.component';
import { HistoriaClinicaFormAntecedentesPersonalesComponent } from './components/admin/historia-clinica/historia-clinica-form-antecedentes-personales/historia-clinica-form-antecedentes-personales.component';
import { HistoriaClinicaFormExamenFisicoComponent } from './components/admin/historia-clinica/historia-clinica-form-examen-fisico/historia-clinica-form-examen-fisico.component';
import { HistoriaClinicaFormPacienteComponent } from './components/admin/historia-clinica/historia-clinica-form-paciente/historia-clinica-form-paciente.component';
import { HistoriaClinicaFormCategorizacionRiesgoInfeccionComponent } from './components/admin/historia_clinica/historia-clinica-form-categorizacion-riesgo-infeccion/historia-clinica-form-categorizacion-riesgo-infeccion.component';
import { HistoriaClinicaFormParaclinicosComponent } from './components/admin/historia_clinica/historia-clinica-form-paraclinicos/historia-clinica-form-paraclinicos.component';
import { HistoriaClinicaFormProcesoAleatorizacionComponent } from './components/admin/historia_clinica/historia-clinica-form-proceso-aleatorizacion/historia-clinica-form-proceso-aleatorizacion.component';
import { HistoriaClinicaFormProcesoVacunacionComponent } from './components/admin/historia_clinica/historia-clinica-form-proceso-vacunacion/historia-clinica-form-proceso-vacunacion.component';
import { InicioAdminComponent } from './components/admin/inicio-admin/inicio-admin.component';
import { VacunadosFormAdminComponent } from './components/admin/vacunados/vacunados-form-admin/vacunados-form-admin.component';
import { VacunadosListAdminComponent } from './components/admin/vacunados/vacunados-list-admin/vacunados-list-admin.component';
import { LoginComponent } from './components/public/login/login.component';


const routes: Routes = [
  { path: 'login', component:  LoginComponent },
  //ADMIN //
  { path: 'inicio', component:  InicioAdminComponent },


  { path: 'historia_clinica/admin', component:  HistoriaClinicaCedulaFormComponent },
  { path: 'historia_clinica/admin/form/paciente/:id', component:  HistoriaClinicaFormPacienteComponent },
  { path: 'historia_clinica/admin/form/antecedentes_personales/:id', component:  HistoriaClinicaFormAntecedentesPersonalesComponent },
  { path: 'historia_clinica/admin/form/examen_fisicos/:id', component:  HistoriaClinicaFormExamenFisicoComponent },
  { path: 'historia_clinica/admin/form/paraclinicos/:id', component:  HistoriaClinicaFormParaclinicosComponent },
  { path: 'historia_clinica/admin/form/categorizacion_riesgo_infeccion/:id', component:  HistoriaClinicaFormCategorizacionRiesgoInfeccionComponent },
  { path: 'historia_clinica/admin/form/proceso_aleatoriazacion/:id', component:  HistoriaClinicaFormProcesoAleatorizacionComponent },
  { path: 'historia_clinica/admin/form/proceso_vacunacion/:id', component:  HistoriaClinicaFormProcesoVacunacionComponent },

  { path: 'vacunados/admin/list', component:  VacunadosListAdminComponent },
  { path: 'vacunados/admin/form', component:  VacunadosFormAdminComponent },
  { path: 'vacunados/admin/edit/:id', component:  VacunadosFormAdminComponent },






  { path: '**', pathMatch: 'full', redirectTo: 'login' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
